.CheckButtonList {
    display: grid;
    grid-template-columns: 50% 50%;
}

.CheckButtonList .ant-checkbox-group-item {
    text-align: left;
    white-space: nowrap;
}

.CheckButtonList .ant-checkbox-wrapper {
    font-size: 20px;
}



